<template>
  <div>
    <!-- <button
      type="button"
      class="btn btn-primary"
      data-bs-toggle="modal"
      data-bs-target="#B_MenuRevise"
    >
      수정
    </button>
    <div
      class="modal fade"
      id="B_MenuRevise"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    > -->
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">메뉴 수정하기</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="saveChanges">
            <div class="mb-2" v-if="menuInfo">
              <label for="menuName" class="form-label"
                >현재 메뉴 이름 {{ fname }}</label
              > 
              <input
                type="text"
                class="form-control"
                id="menuName"
                v-model="fname"
                required
              />
            </div>

            <div class="mb-3">
              <label for="menuDescription" class="form-label">메뉴 설명</label>
              <textarea
                class="form-control"
                id="menuDescription"
                v-model="finfo"
                required
              ></textarea>
            </div>

            <div class="mb-3">
              <label for="menuPrice" class="form-label">메뉴 가격</label>
              <input
                type="text"
                class="form-control"
                id="menuPrice"
                v-model="fprice"
                required
                pattern="[0-9,]+"
                @input="checkIfNumber"
              />
              <div v-if="!isNumber" class="text-danger">
                숫자만 입력해주세요.
              </div>
            </div>

            <div class="mb-3">
              <label class="form-check-label">옵션 추가 여부:</label>
              <div class="form-check">
                <input
                  type="radio"
                  id="optionYes"
                  class="form-check-input"
                  v-model="foption"
                  value="1"
                />
                <label for="optionYes" class="form-check-label"
                  >옵션 추가</label
                >
              </div>
              <div class="form-check">
                <input
                  type="radio"
                  id="optionNo"
                  class="form-check-input"
                  v-model="foption"
                  value="2"
                />
                <label for="optionNo" class="form-check-label"
                  >옵션 추가 안 함</label
                >
              </div>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="saveChanges"
            :disabled="!isNumber"
          >
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
</template>
  

<script>
import axios from "axios";

export default {
  name: "B_MenuRevise",
  props: ["fnum"],
  data() {
    return {
      menuInfo: null,
      fname: "",
      finfo: "",
      fprice: "",
      foption: "2",
      isNumber: true,
    };
  },
  mounted() {
    // 컴포넌트가 마운트될 때 메뉴 정보를 가져오도록 합니다.
    this.fetchMenuInfo();
  },
  watch: {
    fnum: {
      immediate: true,
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          this.fetchMenuInfo();
        }
      },
    },
  },
  methods: {
    async fetchMenuInfo() {
  try {
    const response = await axios.get(`/B_MenuReInfo?fnum=${this.fnum}`);
    this.menuInfo = response.data;
    if (this.menuInfo) {
      // 가져온 메뉴 정보를 데이터에 설정합니다.
      this.fname = this.menuInfo.fname;
      this.finfo = this.menuInfo.finfo;
      this.fprice = this.menuInfo.fprice;
      this.foption = this.menuInfo.foption;
      console.log("Fetched menu info:", this.menuInfo);
      console.log("fnum:", this.fnum);
    }
  } catch (error) {
    console.error("Error fetching menu data:", error);
  }
},


    // checkIfNumber() {
    //   const formattedPrice = this.fprice.replace(/\D/g, "");
    //   this.fprice = Number(formattedPrice).toLocaleString();
    //   this.isNumber = /^[0-9,]+$/.test(formattedPrice);
    // },

    // saveChanges() {
    //   if (!this.isNumber) return;

    //   const formData = new FormData();
    //   formData.append("fname", this.fname);
    //   formData.append("finfo", this.finfo);
    //   formData.append("fprice", this.fprice.replace(/,/g, ""));
    //   formData.append("foption", this.foption);

    //   axios
    //     .post("/MenuUpdate", formData, {
    //       headers: {
    //         "Content-Type": "application/json",
    //       },
    //     })
    //     .then((response) => {
    //       console.log("메뉴 수정 성공:", response.data);
    //     })
    //     .catch((error) => {
    //       console.error("메뉴 수정 실패:", error);
    //     });
    // },
  },
};
</script>
