<template>
  <div
    class="card"
    v-for="(menu, index) in menus"
    :key="index"
    @click="show(menu.fnum)"
  >
    <div class="card-body">
      <!-- 분류 넣고 싶음 -->
      <h5 class="card-title">{{ menu.fname }}</h5>
      <p class="card-text">{{ menu.fprice }} 원</p>
      <p class="card-text">{{ menu.finfo }}</p>
      <!-- <button type="button" >수정</button> -->
      <B_MenuRevise :fnum="menu.fnum" />
      <!-- <p class="card-text"><small class="text-body-secondary">Last updated 3 mins ago</small></p> -->
    </div>
  </div>
</template>
<script>
import axios from "axios";
import B_MenuRevise from "./MS_B_MenuRevise.vue";

export default {
  name: "B_MenuPluse",
  components: {
    B_MenuRevise,
  },
  props: ["bnum"], // bnum을 props로 받아옵니다.
  emits: ["menuSelected"], // menuSelected 이벤트를 명시적으로 선언합니다.
  data() {
    return {
      menus: [],
    };
  },
  created() {
    this.menuList();
  },
  methods: {
    async menuList() {
      try {
        // bnum을 활용하여 해당 가게의 메뉴를 가져옵니다.
        const res = await axios.get(`/B_MenuInfo?bnum=${this.bnum}`);
        this.menus = res.data.sort(
          (a, b) => new Date(b.fnum) - new Date(a.fnum)
        );
        console.log("Response: ", res.data);
      } catch (error) {
        console.log("Error fetching menus:", error);
      }
    },
    showMenu(menuFnum) {
      // menuSelected 이벤트를 발생시킵니다.
      this.$emit("menuSelected", menuFnum);
    },
  },
};
</script>
